import React, { useState, useEffect, useContext } from 'react';
import { Button } from '@material-ui/core';
import { ThemeContext } from '../../../utils/store';
import GameItem1 from '../../../assets/Landing/game1.png';
import GameItem2 from '../../../assets/Landing/game2.png';
import GameItem3 from '../../../assets/Landing/game3.png';
import GameItem4 from '../../../assets/Landing/game4.png';
import GameItem5 from '../../../assets/Landing/game5.png';
import GameItem6 from '../../../assets/Landing/game6.png';
import GameItem7 from '../../../assets/Landing/game7.png';
import GameItem8 from '../../../assets/Landing/game8.png';

import styles from './style.module.scss';

const propTypes = {};

const GamesSection = () => {

	const { theme } = useContext(ThemeContext);
	const [lightMode, setLightMode] = useState(theme);

	const gameImages = [GameItem1, GameItem2, GameItem3, GameItem4, GameItem5, GameItem6, GameItem7, GameItem8];
	const gameTitles = ['Powerball', 'Tile manipulation', 'Powerball', 'Tile manipulation', 'Powerball', 'Tile manipulation', 'Powerball', 'Tile manipulation'];

	useEffect(() => {
		setLightMode(theme);
	}, [theme]);

	const Arrow = () => {
		return (
			<svg width="14" height="11" viewBox="0 0 14 11" fill="none" xmlns="http://www.w3.org/2000/svg">
				<path d="M8.15027 10.9285C8.36121 10.9285 8.56555 10.8428 8.74353 10.6648L13.0677 6.35376C13.2325 6.18896 13.3314 5.95825 13.3314 5.72754C13.3314 5.49683 13.2325 5.25952 13.0677 5.10132L8.75671 0.790283C8.57214 0.605713 8.36121 0.52002 8.15027 0.52002C7.66248 0.52002 7.3197 0.869385 7.3197 1.32422C7.3197 1.57471 7.42517 1.77246 7.57678 1.93066L9.07312 3.43359L10.7408 4.96289L9.25769 4.88379H1.5387C1.01794 4.88379 0.668579 5.22656 0.668579 5.72754C0.668579 6.22852 1.01794 6.57129 1.5387 6.57129H9.25769L10.7408 6.4856L9.07312 8.02148L7.57678 9.51782C7.42517 9.67603 7.3197 9.87378 7.3197 10.1243C7.3197 10.5857 7.66248 10.9285 8.15027 10.9285Z" fill="white" />
			</svg>
		);
	}

	return (
		<div className={styles.gamesSection}>
			<div className={styles.title}>Games</div>
			<Button className={styles.moregameButton}>
				More games&nbsp;&nbsp;&nbsp;<Arrow />
			</Button>
			<div className={styles.cardList}>
				{gameImages.map((item, index) =>
					<div className={styles.card} key={index}>
						<img src={item} alt="collection" className={styles.cardImage} />
						<div className={styles.cardTitle}>{gameTitles[index]}</div>
					</div>
				)}
			</div>
		</div>
	);
}

GamesSection.propTypes = propTypes;

export default GamesSection;
