import React from "react";
import { Route, Switch } from 'react-router-dom';
import Navigation from "../Navigation";
import HomePage from "../Home";
import LandingPage from "../Landing";
import KanonLandingPage from "../KanonLanding";
import * as ROUTES from "../../constants/routes"
import { ThemeContextProvider } from "../../utils/store";

const Main = () => {
	return (
		<ThemeContextProvider>
			<Navigation />
			<Switch>
				<Route path={ROUTES.HOME} component={HomePage} exact />
				<Route path={ROUTES.LANDING} component={KanonLandingPage} exact />
				<Route path={ROUTES.LANDING2} component={LandingPage} exact />
				<Route component={KanonLandingPage} />
			</Switch>
		</ThemeContextProvider>
	);
}


export default Main;
